import React from 'react';

import { useStore } from '../../store/DataProvider';
import './style.css';

const Confidentiality = () => {
  const { state: { texts }} = useStore();

    return(
        <div className='flexible jCenter'>
            <div className='dataParent'>
                <div className='conf-title-parent flexible vertical aCenter jCenter'>
                    <span className='title'>Datenschutz</span>

                </div>
              <div
                className='text-container'
                dangerouslySetInnerHTML={{ __html: texts.find(txt => txt.key === 'DATA_PROTECTION_DESCRIPTION').text }}/>
            </div>
        </div>
    )
}
export default Confidentiality
