import React from 'react';
import { Link } from 'react-router-dom';
import Slider from 'react-slick';

import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import './style.css';

import {
    slide1,
    slide2,
    slide3,
    slide4,
    slide5,
    slide6,
    slide7,
} from '../../../assets/HomePage';

import Image from '../../../components/Image';

const images = [
    {
        image:slide1,
        id:1,
        text: 'WIMPERNVERLÄNGERUNG'
    },
    {
        image: slide2,
        id:2,
        text: 'WIMPERNLIFTING'
    },
    {
        image: slide3,
        id: 3,
        text: 'DAUERHAFTE HAARENTFERNUNG'
    },
    {
        image: slide4,
        id: 4,
        text: 'NADEL ELEKTRO  EPILATION'
    },
    {
        image: slide5,
        id: 5,
        text: 'DAUERHAFTE HAARENTFERNUNG'
    },
    {
        image: slide6,
        id: 6,
        text: 'NAGELDESIGN'
    },
    {
        image: slide7,
        id: 7,
        text: 'MICROBLADING'
    },
];
const items = images.map((item) => (
   <div key={item.id}  className='flexible vertical jCenter itemParent' >
          <Image className='carousel-item' src={item.image} />
      <div className='item-text flexible jCenter'>
          <span >{item.text}</span>
      </div>
   </div>

));
const Carousel = (props) => (
    <div className='AppWrapper'>
        <Slider
            settings={{
                infinite: true,
                speed: 2000,
                slidesToShow: 1,
                slidesToScroll: 1,
                arrows: true,
                responsive: [
                    {
                        breakpoint: 320,
                        settings: {
                            arrows: false
                        }
                    }
                    ]
            }}
        >
          {props.sliders.map(item => {
            if (item.isSlide) {

              if(item.link) {
                return (
                  <Link to={item.link}>
                    <div>
                      <div key={item._id}  className='flexible vertical jCenter itemParent' >
                        <Image className='carousel-item' src={`http://localhost:8080/${item.image}`} />
                        <div className='item-text flexible jCenter'>
                          <span >{item.text}</span>
                        </div>
                      </div>
                    </div>
                  </Link>
                );
              } else {
                return (
                  <div>
                    <div key={item._id}  className='flexible vertical jCenter itemParent' >
                      <Image className='carousel-item' src={`http://localhost:8080/${item.image}`} />
                      <div className='item-text flexible jCenter'>
                        <span >{item.text}</span>
                      </div>
                    </div>
                  </div>
                );
              }
            }

            return null;
          })}
        </Slider>
    </div>
);

export default Carousel;


