import React from 'react';

import './style.css';

import {Image, InfoCard, LineText} from '../../components';

const ServicePage = ({ page }) => {
  const { title, subTitle, description, image, points, prices } = page;
  return (
    <article className="ElectricEpilation">
      <section className="size">
        <div className='sectionsTitleBlock'>
            <p className='sectionsGoldStyle'>
              {title}
            </p>
            <p className='sectionsTitleSmallGold'>{subTitle}</p>
            <p className='secondaryGrey' dangerouslySetInnerHTML={{ __html: description }}/>
        </div>
        {image && image !== 'undefined' && <div className='imageContent flexible jCenter'>
          <Image src={image} width='100%' height='250px' />
        </div>}
        {[...points].sort((a,b) => b.order - a.order).map(item => (
          <div className="row flexible aStart jBetween">
            <LineText
              lineStyle="1px solid rgb(166, 129, 91)"
              headTitle={item.title}
              className='lineTextWidth'
              width=""/>

            <InfoCard
              textData={item.description}
              bgColor="rgba(236,204,164,0.41)"
            />
          </div>
        ))}
        <div className='shelackPrices'>
          {[...prices].sort((a,b) => b.order - a.order).map(cat => (
            <>
              <LineText
                lineStyle="1px solid"
                headTitle={cat.title}
                textColor=' rgb(166, 129, 91)'
                flexName='flexible grow '
                width="100%"
              />
              {[...cat.data].sort((a,b) => b.order - a.order).map(price => (
                <LineText
                  lineStyle="1px dashed"
                  textTitleLeft={<span dangerouslySetInnerHTML={{ __html: price.description }}/>}
                  textTitleRight={price.price > 0 ? `${price.price} €` : null}
                  flexName="flexible jBetween aCenter"
                  width="100%"/>
              ))}
            </>
          ))}
        </div>
      </section>
    </article>
  )
}
export default ServicePage;
