import React from 'react';
import { Link } from 'react-router-dom';

import Services from '../Services';

import { useStore } from '../../store/DataProvider';

import { rrs } from '../../assets/HomePage';

import './style.css';

const Main = () => {
  const { state: { texts, sliders } } = useStore();

  const findText = (key) => {
    return texts.find(txt => txt.key === key).text;
  };
    const mainImage = sliders.find(item => !item.isSlide);
    return(
        <div className='Main' >
          <div
            className='main-first flexible aCenter jCenter vertical leis'
            style={{backgroundImage:`url(${mainImage ? `http://localhost:8080/${mainImage.image}` : rrs})`}}
          >
            <span
              className='italic-text'
              style={{
                maxWidth: 500,
                textAlign: 'center'
              }}
            >
              {findText('HOME_HEADER_TEXT')}
            </span>
          </div>

          <div className='main-second size'>
            <div className='flexible jCenter'>
               <Link to='/contact'>
                   <button className='button'>
                     {findText('HOME_CONTACT_BUTTON_TEXT')}
                   </button>
               </Link>

            </div>
            <div className='flexible jCenter'>
                <span className='second-title'>{findText('HOME_DESCRIPTION_LABEL')}</span>
            </div>
            <span className='second-text flexible jCenter' id='leistungen'>
              <div dangerouslySetInnerHTML={{ __html: findText('HOME_DESCRIPTION') }} />
            </span>
          </div>
          <div >
            <div className='flexible jCenter'>
              <span className='title'>{findText('HOME_SLIDER_LABEL')}</span>
            </div>
            <div className='flexible aCenter jCenter'>
              <Services sliders={sliders} />
            </div>
            <div className='flexible jCenter'>
              <Link to='/DateBooking'>
                <button className='button'>
                  {findText('HOME_BOOKING_BUTTON_TEXT')}
                </button>
              </Link>
            </div>
          </div>
        </div>
    )
}
export default Main


